* {
  box-sizing: border-box;
}
.calculator {
  border: 1px solid #433437;
  border-radius: 5px;
}

.calculator-input {
  background-color: #433437;
  color: #fff;
  text-align: right;
  font-size: 30px;
  height: 60px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  align-items: center;
  padding-right: 10px;
  border-radius: 10px;
}

.calculator-controls-operations {
  background-color: #F2274F;
  border: 1px solid #433437;
  font-size: 20px;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  cursor: pointer;
  border-radius: 10px;
}

.calculator-controls-operations:hover {
  background-color: rgb(102, 51, 153);
}

.calculator-controls-input {
  padding: 10px 0;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #433437;
  cursor: pointer;
  border-radius: 10px;
}

.calculator-controls-input:hover {
  background-color: #433437;
  color: #fff;

}

